@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

*{
    font-family: "Open Sans", sans-serif;
    margin:0;
    padding:0;
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    background-color: #f7f7f7;
}

.hide{
    display:none;
}

.show{
    display:block;
}

.main-container{
    display: flex;
    flex-direction: column;
    padding:10% 20%;
    align-items: center;
}

.return-button{
    font-size: 3rem;
    font-weight: 300;
    padding:4% 30px;
    border:none;
}

.search-bar{
    font-size: 2rem;
    padding:0.7% 2%;
    margin-top:4%;
    margin-bottom:10%;
}

.food-boxes{
    display: flex;
    align-items: center;
    width:100%;
    padding:5% 0;
}

.food-name{
    font-size: 4rem;
    padding:0 3%;
    width:100%;
}

.food-select-button{
    font-size:1.8rem;
    font-weight: 600;
    padding:1% 3%;
    color:white;
    background-color: orangered;
    border:3px black solid;
    border-radius: 10px;
}

.title{
    font-size: 6rem;
}

.title .span1{
    color:orangered;
}

.description{
    font-size: 2.6rem;
    width:100%;
    padding:8% 2.7%;
}

.typewriter p{
    padding-right: 28px;
    overflow: hidden;
    border-right: .15em solid black;
    white-space: nowrap;
    margin: 0 auto;
    letter-spacing: .15em;
    animation: typing 2s steps(1000, end), blink-caret .75s step-end infinite;
}

@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

@keyframes blink-caret {
    from, to { border-color: orangered; }
    50% { border-color: orangered; }
}

.form{
    display: flex;
    flex-direction: column;
}

.form input{
    font-size: 2rem;
    width:60vw;
    padding-bottom:300px;
    padding-left:2%;
    margin-top:40%;
}

.form-buttons button{
    font-size: 2rem;
    margin-block:18px;
    margin-right:50px;
    padding:1% 3%;
}

.form-button-two{
    font-size:1.8rem;
    font-weight: 600;
    padding:1% 3%;
    color:white;
    background-color: orangered;
    border:3px black solid;
    border-radius: 10px;
}

.form-button-one{
    font-size:1.8rem;
    font-weight: 600;
    padding:1% 3%;
    border:3px black solid;
    border-radius: 10px;
}


.img{
    width:100vw;
    padding-block: 8%;
}

.team-heading{
    padding-top:50%;
    font-size: 4rem;
}

.unordered-list{
    list-style: none;
}

.unordered-list li{
    font-size: 3rem;
    text-transform: uppercase;
    font-weight: 300;
    letter-spacing: 3px;
    animation:3s hello infinite
}

@keyframes hello {
    50%{letter-spacing: 4px;}
}

.show-more{
    font-size:1.8rem;
    font-weight: 600;
    padding:1% 3%;
    border:3px black solid;
    border-radius: 10px;
    align-self: flex-end;
    margin-top:5%;
}

.show{
    display: flex;
    align-items:center;
    padding-top:25%;
}

.last-box-1, .last-box-2{
    width:50%;
    
}
.last-box-1{
    font-size: 2rem;
    line-height: 70px;
}

.last-box-1 span{
    color:orangered;
}

.last-box-1 button{
    width:100%;
    margin-block: 4%;
    padding:1% 3%;
    border:3px black solid;
    border-radius: 10px;
}

.last-box-1 img{
    width:60px;
}
    
.last-box-2 img{
    border-radius: 1000px;
}

@media(min-width:1000px){
    *{
        zoom:0.95;
    }
    .show{
        padding-top:5%;
    }
}